body,html{
    font-family: Arial, Helvetica, sans-serif;
    background-color: coral;
    font-size: 27px;
    font-weight: 700;
    margin: 0;
}
.wrapper{
    text-align: center;
}
.title{
    width: auto;
    height: auto;
    padding: 0 10px 0;
    margin: 0;
    margin-bottom: 20px;
    box-sizing: border-box;
    background: rgb(248, 229, 200);
    border: none;
    font-size: 50px;
    border-radius: 5px;
    font-weight: 700;
    color: #793535;
}
input {
    margin: 0 10px;
    margin-left: 20px;
    position: relative;
    height: 20px;
    width: 20px;
    color: darkcyan;
    border: 4px solid;
    border-radius: 10px;
    appearance: none;
    outline: 0;
    cursor: pointer;
    transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
    font-size: 13px;
}

input:checked {
    background-color: #793535;
}

.textBox{
    width: 270px;
    height: 60px;
    padding: 0 20px 0;
    box-sizing: border-box;
    border: none;
    background: sandybrown;
    color: #793535;
    font-weight: 700;
    font-size: 20px;
    line-height: 60px;
    margin: 10px 25px;
    cursor: auto;
}

button{
    width: auto;
    height: 30px;
    padding: 0 10px 0;
    margin: 5px;
    box-sizing: border-box;
    background: rgb(248, 229, 200);
    border: none;
    font-size: 20px;
    line-height: 30px;
    border-radius: 5px;
    font-weight: 700;
    color: #793535;
    cursor: pointer;
}

.leftToDo{
    padding: 20px 0;
    text-align: center;
    color: rgb(248, 229, 200);
    font-size: 20px;
    font-weight: 700;
}